@import "../../common/colors.scss";

.header {
  background-color: $secondary-color;
  width: 100vw;
  height: 72px;
  padding: 0px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: $font-primary;
  display: flex;
  align-items: center;
  width: 90px;
  height: inherit;
  img {
    width: 100%;
    height: 90%;
    object-fit: contain;
  }
}

.user-image,.user-image img {
width: 58px;
height: 48px;
border-radius: 50%;
}

.search-bar {
  width: 50%;
  display: flex;
  justify-content: center;

  form {
    display: flex;
    justify-content: center;
    width: 70%;
  }

  button {
    padding: 0px 8px;
    font-size: 20px;
    cursor: pointer;
    height: 38px;
    margin-left: 2px;
    border: none;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;


  }

  input {
    font-size: 18px;
    width: 100%;
    padding: 5px 5px 5px 10px;
    height: 38px;
    outline: none;
    border: none;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
}